import React from "react";
import { Handle, Position } from "@xyflow/react";
import { useAppContext } from "../../context/AppContext";
import toast from "react-hot-toast";

interface Props {
  id: string;
  data: any;
}

const CustomNode: React.FC<Props> = ({ id, data }) => {
  const { addHobbyToUser, deleteUser } = useAppContext();

  const onDragOver = (e: React.DragEvent) => e.preventDefault();

  const onDrop = async (e: React.DragEvent) => {
    e.preventDefault();
    const hobby = e.dataTransfer.getData("text/hobby");
    if (!hobby) return;

    try {
      await addHobbyToUser(id, hobby);
      toast.success(`Added hobby "${hobby}"`);
    } catch {
      toast.error("Failed to add hobby");
    }
  };

  // Dynamic size and color intensity
  const popularity = data.popularityScore ?? 0;
  const intensity = Math.min(popularity / 10, 1);
  const baseSize = 120 + popularity * 5;
  const bgColor =
    popularity > 10
      ? `rgba(34,197,94,${0.5 + intensity * 0.5})` // green for very high
      : popularity > 5
      ? `rgba(59,130,246,${0.5 + intensity * 0.5})` // blue for medium
      : `rgba(147,197,253,${0.4 + intensity * 0.4})`; // light blue for low

  return (
    <div
      onDragOver={onDragOver}
      onDrop={onDrop}
      style={{
        width: baseSize,
        padding: 10,
        borderRadius: 10,
        background: bgColor,
        color: "#fff",
        textAlign: "center",
        boxShadow: "0 4px 10px rgba(0,0,0,0.1)",
        transition: "all 0.3s ease",
      }}
    >
      <div style={{ fontWeight: 700 }}>{data.username}</div>
      <div style={{ fontSize: 12 }}>{data.age} yrs</div>
      <div style={{ fontSize: 11, opacity: 0.9 }}>
         Score: {data.popularityScore ?? 0}
      </div>

      <button
        onClick={async () => {
          const confirmDel = window.confirm(
            `Delete user "${data.username}"? This will unlink all friends first.`
          );
          if (!confirmDel) return;

          try {
            await fetch(
              `${
                process.env.REACT_APP_API_URL || "http://localhost:5000/api"
              }/users/${id}/unlinkAll`,
              { method: "DELETE" }
            );
            await deleteUser(id);
            toast.success("User deleted successfully");
          } catch {
            toast.error("Failed to delete user");
          }
        }}
        style={{
          border: "none",
          background: "rgba(0,0,0,0.3)",
          color: "#fff",
          padding: "4px 8px",
          borderRadius: 6,
          cursor: "pointer",
          fontSize: 12,
          marginTop: 6,
        }}
      >
        Delete
      </button>

      <Handle type="target" position={Position.Top} />
      <Handle type="source" position={Position.Bottom} />
    </div>
  );
};

export default CustomNode;
